// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue"
import App from "./App.vue"
import router from "./router/index.js"
//mint-ui
import MintUI from "mint-ui"
import "mint-ui/lib/style.css"
//mui
import "./lib/mui/css/mui.css"
import "./lib/mui/css/icons-extra.css"
//axios
import axios from "axios"
//moment
import moment from "moment"
// vant
import Vant from "vant"
import "../node_modules/vant/lib/index.css"
import VuePreview from "vue-preview"
import Vuex from "vuex"


Vue.use(Vant)
Vue.use(Vuex)

Vue.config.productionTip = false
Vue.prototype.$axios = axios;

Vue.use(MintUI)
Vue.use(VuePreview)
/* eslint-disable no-new */

//
var car = JSON.parse(localStorage.getItem('car') || '[]')
const store=new Vuex.Store({
  state:{   //this.$store.state.xxx 
    //data
    car:car   //
  },
  mutations:{   //this.$store.commit.('','')
    //methods
    addToCar(state,goodsinfo){
      //storecar
      var flag=false;
      state.car.some(item=>{
        if(item.id==goodsinfo.id){
          item.count+=parseInt(goodsinfo.count);
          flag=true;
          return true;
        }
      })
      if(!flag){
        state.car.push(goodsinfo);
      }
      localStorage.setItem("car",JSON.stringify(state.car))
    },
    updateGoodsInfo(state,goodsinfo){
      //
      state.car.some(item=>{
        if(item.id==goodsinfo.id){
          item.count=parseInt(goodsinfo.count)
          return true;
        }
      })
      //
      localStorage.setItem("car",JSON.stringify(state.car))
    },
    removeFormCar(state,id){
      //id
      state.car.some((item,i)=>{
        if(item.id==id){
          state.car.splice(i,1)
          return true;
        }
      })
      localStorage.setItem("car",JSON.stringify(state.car))
    },
    updateGoodsSelected(state,info){
      state.car.some(item=>{
        if(item.id==info.id){
          item.selected=info.selected
        }
      })
      localStorage.setItem("car",JSON.stringify(state.car))
    }
  },
  getters:{   //this.$store.getters.xxx
    //
    getAllCount(state){
      var amount=0;
      state.car.forEach(item=>{
        amount+=item.count;
      })
      return amount;
    },
    getGoodsCount(state){
      var otto={}
      state.car.forEach(item=>{
        otto[item.id]=item.count
      })
      return otto
    },
    getGoodsSelected(state){
      var otto={}
      state.car.forEach(item=>{
        otto[item.id]=item.selected
      })
      return otto
    },
    getGoodsCountAndAmount(state){
      var otto={
        count:0,  //
        amount:0  //
      }
      state.car.forEach(item=>{
        if(item.selected){
          otto.count+=item.count
          otto.amount+=item.price*item.count
        }
      })
      return otto
    }
  }
})

Vue.filter("dateFormat", function(dateStr,patten="YYYY-MM-DD HH:mm:ss"){
  return moment(dateStr).format(patten)
})

new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})



