<template>
  <div class="goodsinfo-container">
    <!--  -->
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :lunbotuList="lunbotu" :isfull="false"></swiper>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{goodsinfo.title}}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            :
            <del>{{goodsinfo.market_price}}</del> &nbsp;&nbsp; :
            <span class="now-price">{{goodsinfo.sell_price}}</span>
          </p>
          <p class="goods-num">
            :
            <van-stepper v-model="value" input-width="55px" button-size="32px" :max="goodsinfo.stock_quantity"/>
          </p>
          <p>
            <mt-button type="primary" size="small" @click="todo"></mt-button>
            <mt-button type="danger" size="small" @click="addToShopCar"></mt-button>
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>:{{goodsinfo.goods_no}}</p>
          <p>:{{goodsinfo.stock_quantity}}</p>
          <p>:{{goodsinfo.add_time | dateFormat}}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
        <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import swiper from "../subcomponents/swiper.vue";
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      value: 1,
      id: this.$route.params.id, //id
      lunbotu: [],
      goodsinfo: {},
      ballFlag: false, //
    };
  },
  created() {
    this.getLunbo();
    this.getGoodsInfo();
  },
  methods: {
    getLunbo() {
      this.$axios({
        url: "http://www.liulongbin.top:3005/api/getthumimages/" + this.id,
        methods: "GET"
      }).then(res => {
        res.data.message.forEach(item => {
          item.img = item.src;
        });
        this.lunbotu = res.data.message;
      });
    },
    getGoodsInfo() {
      this.$axios({
        url: "http://www.liulongbin.top:3005/api/goods/getinfo/" + this.id,
        method: "GET"
      }).then(res => {
        if (res.data.status === 0) {
          this.goodsinfo = res.data.message[0];
        }
      });
    },
    goDesc(id) {
      //
      this.$router.push({ name: "goodsinfo", params: { id } });
    },
    goComment(id) {
      //
      this.$router.push({ name: "goodscomment", params: { id } });
    },
    addToShopCar() {
      //
      this.ballFlag = !this.ballFlag;
      // 
      const goodsinfo={id:this.id,count:this.value,price:this.goodsinfo.sell_price,selected:true};
      this.$store.commit("addToCar",goodsinfo)
    },
    beforeEnter(el) {
      el.style.transform = "translate(0,0)";
    },
    enter(el, done) {
      el.offsetWidth;

      //
      const ballPosition = this.$refs.ball.getBoundingClientRect(); //
      const badgePosition = document
        .getElementById("badge")
        .getBoundingClientRect(); //
      const xDist = badgePosition.left - ballPosition.left;
      const yDist = badgePosition.top - ballPosition.top;
      el.style.transform = `translate(${xDist}px,${yDist}px)`;
      el.style.transition = "all 0.5s ease";
      el.addEventListener("transitionend", done);
      // done();       //done() 
    },
    afterEnter(el) {
      this.ballFlag = !this.ballFlag;
    },
    todo(){
      Toast("");
    }
  },
  components: {
    swiper
  }
};
</script>
<style>
.goodsinfo-container {
  background-color: #eee;
  overflow: hidden;
}
.now-price {
  color: red;
  font-size: 16px;
  font-weight: bold;
}
.van-stepper .van-stepper__input{
  margin-bottom: 0px;
}
.mui-card-footer {
  display: block;
}
.mui-card-footer button {
  margin: 15px 0;
}
.ball {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  top: 400px;
  left: 146px;
  z-index: 100;
}
</style>


